<template>
    <div class="AccessRowRegular" :class="computedClass">
        <div class="email">
            {{ account.email }}
        </div>
        <div class="date">
            {{ formattedDate }}
        </div>
        <div class="activity">
            {{ isActive ? "ACTIVE" : "NOT ACTIVE" }}
        </div>
        <div class="dropdown-trigger" v-if="isActive">
            <Arrow
                :color="isCardAdded ? 'primary' : 'white'"
                :opened="opened"
                @click="$emit('click', $event)" />
        </div>
        <div class="checkbox" :class="{ 'enabled': isCheckboxEnabled }" v-else>
            <Checkbox
                :checked="checked && isCheckboxEnabled"
                :disabled="!isCheckboxEnabled"
                :highlightError="highlightError"
                @click="$emit('check', checked)" />
        </div>
        <SlideUpDown :active="opened" class="dropdown-content">
            <AccessPaymentDetails v-if="isPaymentActive"
                :payment="account.payment"
                @deletecard="$emit('deletecard', $event)"
                @addcard="$emit('addcard', $event)" />
        </SlideUpDown>
    </div>
</template>

<script>
    import SlideUpDown from 'vue-slide-up-down';
    import AccessPaymentDetails from './AccessPaymentDetails.vue';
    import Arrow from '../Arrow.vue';
    import Checkbox from '../input/Checkbox.vue';
    import { PAYMENT_CARD, PAYMENT_CASH } from '../../types/account.types';

    /**
     * Formats the date like 00.00.0000
     * @param {Date} date
     */
    function formatDate(date) {
        const day = date.getDate();
        const monthIndex = date.getMonth() + 1;
        const year = date.getFullYear();

        const pad = number => (number > 9 ? `${number}` : `0${number}`);

        return `${pad(day)}-${pad(monthIndex)}-${year}`;
    }

    export default {
        name: 'AccessRowRegular',
        components: {
            SlideUpDown,
            AccessPaymentDetails,
            Arrow,
            Checkbox,
        },
        props: {
            account: {
                required: true,
            },
            editable: {
                type: Boolean,
                default: false,
            },
            opened: {
                type: Boolean,
                default: false,
            },
            checked: {
                type: Boolean,
                default: false,
            },
            tabs: {
                required: true,
            },
            highlightError: {
                type: Boolean,
                default: false,
            },
        },
        data() {
            return {
                expiryDate: new Date(),
                items: [
                    { label: '12' },
                    { label: '6' },
                    { label: '1' },
                ],
            };
        },
        computed: {
            computedClass() {
                return {
                    opened: this.opened,
                };
            },
            isCardAdded() {
                return this.account.payment !== undefined
                    && this.account.payment.type === PAYMENT_CARD
                    && this.account.payment.cardNumber !== undefined;
            },
            isPaymentActive() {
                return this.account.payment !== undefined;
            },
            formattedDate() {
                return formatDate(this.account.expiryDate);
            },
            isCheckboxEnabled() {
                return this.isPaymentActive
                    && this.account.payment.type === PAYMENT_CARD
                    && this.account.payment.cardNumber === undefined;
            },
            isActive() {
                return (this.account.payment && this.account.payment.type === PAYMENT_CASH)
                    || (this.account.payment && this.account.payment.type === PAYMENT_CARD
                    && this.account.payment.cardNumber !== undefined);
            },
        },
    };
</script>

