            word-break: break-all;
        }

        .email {
            grid-area: email;
        }

        .date {
            grid-area: date;
            text-align: left;
        }

        .activity {
            grid-area: activity;
            color: $dark-text;

            @media only screen and (max-width: $phone-max-width) {
                display: none;
            }
        }

        .dropdown-trigger {
            grid-area: action;
            display: flex;
            align-items: flex-start;
            justify-content: center;

            img {
                flex: 0 1 auto;
                width: 30px;
            }

            .Arrow {
                cursor: pointer;
            }
        }

        .checkbox {
            grid-area: action;
            display: flex;
            justify-content: center;
            align-items: flex-start;

            opacity: 0;

            &.enabled {
                opacity: 1;
            }
        }

        .dropdown-content {
            grid-area: dropdown;

            .dropdown-content-wrapper {
                display: flex;
                justify-content: space-between;

                .dropdown-element {
                    margin: 40px 0;
                }

                .dropdown-element:not(:last-child) {
                    border-right: 1px solid darken($dark-text, 25);
