                    }

                    .dropdown-element:not(:last-child) {
                        border: none;
                        border-bottom: 1px solid darken($dark-text, 25);
                    }
                }
            }
        }
    }

    @media only screen and (max-width: $phone-max-width) {
        .AccessRowRegular {
            grid-template-areas: 'email date action'
                        'dropdown dropdown dropdown';
            grid-template-columns: 1fr 1fr 40px;
        }
    }
</style>
