<template>
    <div class="AccessPaymentDetails">
        <h1>Opcja wybrana <strong>{{ payment.chosenTabKey }}</strong></h1>
        <header class="payment-details">
            <p class="form">Forma</p>
            <p class="payment-type-label">{{ paymentTypeLabel }}</p>
            <div class="add-card-button" v-if="isCard && !payment.cardNumber"
                @click="cardIsBeingAdded = !cardIsBeingAdded">
                <font-awesome-icon v-if="!cardIsBeingAdded" :icon="['fas', 'plus']" />
                <font-awesome-icon v-else :icon="['fas', 'times']" />
            </div>
            <Arrow class="card-panel-toggle"
                color="white"
                v-if="isCard && payment.cardNumber"
                :opened="cardDetailsOpened"
                @click="cardDetailsOpened = !cardDetailsOpened" />
        </header>
        <CardDetailsForm class="add-card-panel" v-if="cardIsBeingAdded" @submit="addCard" />
        <SlideUpDown v-if="cardIsAdded" class="card-details" :active="cardDetailsOpened">
            <div class="header">Tekst</div>
            <div class="header">Tekst</div>
            <div class="header"></div>
            <div class="field">pole4_tab3(not edit)</div>
            <div class="card-number">{{ hiddenCardNumber }}</div>
            <div class="delete-button" @click="isConfirmDeleteCardPopupEnabled = true">
                <SmallButton borderColor="white">Usu</SmallButton>
            </div>
            <CardDeleteConfirmPopup
            :enabled="isConfirmDeleteCardPopupEnabled"
                @confirm="$emit('deletecard')"
                @close="isConfirmDeleteCardPopupEnabled = false"/>
        </SlideUpDown>
    </div>
</template>

<script>
    import { library } from '@fortawesome/fontawesome-svg-core';
    import { faPlus, faTimes } from '@fortawesome/free-solid-svg-icons';
    import SlideUpDown from 'vue-slide-up-down';
    import Arrow from '../Arrow.vue';
    import SmallButton from '../input/SmallButton.vue';
    import CardDetailsForm from './CardDetailsForm.vue';
    import CardDeleteConfirmPopup from './CardDeleteConfirmPopup.vue';
    import { PAYMENT_CARD } from '../../types/account.types';

    library.add(faPlus, faTimes);

    export default {
        name: 'AccessPaymentDetails',
        components: {
            SlideUpDown,
            Arrow,
            SmallButton,
            CardDetailsForm,
            CardDeleteConfirmPopup,
        },
        props: {
            payment: {
                type: Object,
                required: true,
            },
        },
        data() {
            return {
                cardDetailsOpened: false,
                cardIsBeingAdded: false,
                editableCardNumber: '',
                isConfirmDeleteCardPopupEnabled: false,
            };
        },
        computed: {
            paymentTypeLabel() {
                return this.payment.type === PAYMENT_CARD ? 'Karta' : 'Gotwka';
            },
            isCard() {
                return this.payment.type === PAYMENT_CARD;
            },
            cardIsAdded() {
                return this.payment.type === PAYMENT_CARD && this.payment.cardNumber !== undefined;
            },
            hiddenCardNumber() {
                let hiddenNumber = this.payment.cardNumber.substring(this.payment.cardNumber.lastIndexOf(' '));
                hiddenNumber = `XXXX XXXX XXXX ${hiddenNumber}`;
                return hiddenNumber;
            },
        },
        methods: {
            formatCardNumber() {
                // Removing all non-digit characters.
                const value = this.editableCardNumber.replace(/\D/g, '').substring(0, 16);

                let result = '';
                for (let i = 0; i < value.length; i += 1) {
                    result += value[i];
                    if (i % 4 === 3 && i !== value.length - 1) {
                        result += ' ';
                    }
                }

                this.editableCardNumber = result;
            },
            addCard(options) {
                this.$emit('addcard', options);
                this.cardIsBeingAdded = false;
            },
        },
    };
</script>

