<template>
    <Popup class="BatchAddCardPopup" :enabled="enabled" @close="$emit('close')">
        <div class="tab-summary" v-if="tab === 0">
            <header>
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                    Sed nec dignissim 
                </p>
                <div class="image-placeholder" />
            </header>
            <div class="policies">
                <PolicyRow :checked="areAllChecked" @input="acceptAllInput">
                    Akceptuj wszystkie ponisze zgody
                </PolicyRow>
                <PolicyRow class="inset" :checked="policyStates[0]" @input="policyInput(0, $event)">
                    Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                    Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,
                    when an unknown printer took a galley of type and scrambled it to make
                    a type specimen book. It has survived not only five centuries,
                    but also the leap into electronic typesetting, remaining essentially unchanged.
                    It was popularised in the 1960s with the release of Letraset sheets containing
                </PolicyRow>
                <PolicyRow class="inset" :checked="policyStates[1]" @input="policyInput(1, $event)">
                    Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                    Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,
                    when an unknown printer took a galley of
                </PolicyRow>
                <PolicyRow class="inset" :checked="policyStates[2]" @input="policyInput(2, $event)">
                    Lorem Ipsum is simply dummy text of the printing and typesetting
                </PolicyRow>
            </div>
        </div>
        <div class="tab-proper" v-if="tab === 1" :class="{ 'success': cardAdded }">
            <CardDetailsForm class="content" @submit="handleCardAdded" />
            <div class="success-overlay">
                <div class="check-mark">
                    <font-awesome-icon icon="check" />
                </div>
            </div>
        </div>
        <div class="footer">
            <div class="info">
                <h2 class="youve-chosen">Wybrae:</h2>
                <div class="stats">
                    <h2 class="num-of-selected">{{ selectedCount }}x0</h2>
                    <WierdCounter class="counter" />
                </div>
            </div>
            <div class="actions">
                <BigButton class="btn" borderColor="primary"
                    :disabled="!canProceed"
                    @click="proceed">BUTTON_4</BigButton>
            </div>
        </div>
    </Popup>
</template>

<script>
    import Popup from '../generic/Popup.vue';
    import PolicyRow from '../PolicyRow.vue';
    import CardDetailsForm from './CardDetailsForm.vue';
    import WierdCounter from '../WeirdCounter.vue';
    import BigButton from '../input/BigButton.vue';

    export default {
        name: 'BatchAddCardPopup',
        components: {
            Popup,
            PolicyRow,
            CardDetailsForm,
            WierdCounter,
            BigButton,
        },
        props: {
            enabled: {
                type: Boolean,
                default: false,
            },
            selectedCount: {
                required: true,
            },
        },
        data() {
            return {
                tab: 0,
                policyStates: [false, false, false],
                cardAdded: false,
                cardOptions: null,
            };
        },
        computed: {
            areAllChecked() {
                return !this.policyStates.some(p => p === false);
            },
            canProceed() {
                if (this.tab === 0) {
                    return this.areAllChecked;
                }
                if (this.tab === 1) {
                    return this.cardAdded;
                }
                return false;
            },
        },
        methods: {
            acceptAllInput() {
                if (this.policyStates.some(p => p === false)) {
                    this.policyStates = this.policyStates.map(() => true);
                } else {
                    this.policyStates = this.policyStates.map(() => false);
                }
            },

            policyInput(index) {
                const states = this.policyStates.slice();
                states[index] = !this.policyStates[index];
                this.policyStates = states;
            },

            handleCardAdded(options) {
                this.cardOptions = options;
                this.cardAdded = true;
            },

            proceed() {
                if (this.tab === 0) {
                    if (!this.areAllChecked) {
                        return;
                    }

                    this.tab = 1;
                } else if (this.tab === 1) {
                    this.$emit('submit', this.cardOptions);
                    this.tab = 0;
                    this.policyStates = [false, false, false];
                    this.cardAdded = false;
                    this.cardOptions = null;
                }
            },
        },
    };
</script>

