        components: {
            WierdCounter,
            BigButton,
        },
        props: {
            selectedCount: {
                required: true,
            },
        },
        computed: {
            computedClass() {
                return {
                    opened: this.opened,
                    enabled: this.selectedCount > 0,
                };
