<template>
    <div class="Arrow" @click="$emit('click', $event)" :class="computedClass">
        <img src="@/assets/images/arrow-down-white.svg" v-if="color === 'white'">
        <img src="@/assets/images/arrow-down.svg" v-else>
    </div>
</template>

<script>
    export default {
        name: 'Arrow',
        props: {
            color: {
                type: String,
                default: 'primary',
            },
            opened: {
                type: Boolean,
                default: false,
            },
        },
        computed: {
            computedClass() {
                return {
                    opened: this.opened,
                };
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .Arrow {
        display: flex;
        align-items: center;
        justify-content: center;

        transform: scale(1, 1);
        transition: 0.2s ease-out transform;

        img {
            flex: 0 1 auto;
            width: 30px;
        }

        &.opened {
            transform: scale(1, -1);
        }
    }
</style>
