<template>
    <div class="CardDetailsForm">
        <form @submit.prevent="addCard(false)">
            <DefaultInputField type="text" v-model="cardNumber" placeholder="Numer karty"
                @keyup="formatCardNumber" :maxLength="19" pattern="^[0-9]{4} [0-9]{4} [0-9]{4} [0-9]{4}$" />
            <DefaultInputField type="text" placeholder="Pole_2" v-model="field2" @keyup="formatSecondField" />
            <div class="field-row">
                <div class="date-picker">
                    <DefaultInputField class="field" placeholder="MM" type="text"
                        v-model="month" :maxLength="2" @keyup="formatMonth" 
                        @change="formatMonthPost" />
                    <p class="slash">/</p>
                    <DefaultInputField class="field" placeholder="RR" type="text"
                        v-model="year" :maxLength="2" @keyup="formatYear"
                        @change="formatYearPost" />
                </div>
                <DefaultInputField type="text" placeholder="Pole4" v-model="field4"
                    :maxLength="3"
                    @keyup="formatFourthField" pattern="^[0-9]{3}$" />
            </div>
            <BigButton class="add-button" borderColor="primary" type="submit">Dodaj</BigButton>
        </form>
    </div>
</template>

<script>
    import { extend } from 'vee-validate';
    import { required, digits, regex } from 'vee-validate/dist/rules';
    import DefaultInputField from '../input/DefaultInputField.vue';
    import BigButton from '../input/BigButton.vue';

    extend('required', {
        ...required,
        message: 'To pole jest wymagane.',
    });

    extend('digits', {
        ...digits,
        message: 'Pole musi by trzema cyframi.',
    });

    extend('regex', {
        ...regex,
        message: 'Numer karty nie ma poprawnej formy.',
    });

    function formatNumber(text, max) {
        const number = parseInt(text.replace(/\D/g, ''), 10);

        if (Number.isNaN(number)) {
            return '';
        }

        return `${Math.min(number, max)}`;
    }

    export default {
        name: 'CardDetailsForm',
        components: {
            DefaultInputField,
            BigButton,
        },
        data() {
            return {
                cardNumber: '',
                field2: '',
                field4: '',
                month: '',
                year: '',
            };
        },
        methods: {
            addCard(invalid) {
                if (invalid) {
                    return;
                }

                this.$emit('submit', {
                    cardNumber: this.cardNumber,
                    field2: this.field2,
                    field4: this.field4,
                    month: this.month,
                    year: this.year,
                });

                this.cardNumber = '';
                this.field2 = '';
                this.field4 = '';
                this.month = '';
                this.year = '';
            },

            formatCardNumber() {
                // Removing all non-digit characters.
                const value = this.cardNumber.replace(/\D/g, '').substring(0, 16);

                let result = '';
                for (let i = 0; i < value.length; i += 1) {
                    result += value[i];
                    if (i % 4 === 3 && i !== value.length - 1) {
                        result += ' ';
                    }
                }

                this.cardNumber = result;
            },

            formatSecondField() {
                // Removing all non-letter characters.
                this.field2 = this.field2.replace(/[^a-z ]/ig, '');
            },

            formatFourthField() {
                // Removing all non-digit characters.
                this.field4 = this.field4.replace(/\D/g, '');
            },

            formatMonth() {
                this.month = formatNumber(this.month, 12);
            },

            formatMonthPost() {
                if (this.month.length === 1) {
                    this.month = `0${this.month}`;
                }
            },

            formatYear() {
                this.year = formatNumber(this.year, 99);
            },

            formatYearPost() {
                if (this.year.length === 1) {
                    this.year = `0${this.year}`;
                }
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .CardDetailsForm {
        .field-row {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;

            * {
                flex: 1 1;
                margin-bottom: 0;
            }

            .date-picker {
                flex: 0 0 auto;
                display: flex;
                align-items: flex-end;
                height: 40px;
                margin-right: 30px;

                .field {
                    flex: 0 0 auto;
                    width: 2.8em;
                }

                .slash {
                    position: relative;
                    bottom: 10px;
                    flex: 0 0 auto;
                    margin: 0 10px;
                }
            }

            .error {
                height: 20px;
                margin-top: 5px;
            }
        }

        .add-button {
            display: block;
            width: 100%;
            max-width: 200px;
            margin: 0 auto;
            margin-top: 20px;
        }
    }
</style>
