<template>
    <div>
        <MainPageHeader />
        <PageContent class="content">
            <section class="access-section" v-if="isAdmin">
                <h1>Zarzdzaj dostpem</h1>
                <header>
                    <h3>Nazwa uytkownika</h3>
                    <h3 class="expiry-head">Wano konta</h3>
                </header>
                <AccessRow
                    v-for="(account, index) in accounts"
                    :key="account.key"
                    :account="account"
                    :editable="accountEditModes[account.key]"
                    @edit="toggleEditAccess(account.key)"
                    @save="saveExpiryDate(index, $event)"
                    />
                <div class="change-everyone-access">
                    <h5>Zmie wszystkim</h5>
                    <Dropdown v-on:dropdown="dropdownChoose" :items="items"/>
                    <SmallButton class="change-access-button" border-color="primary" @click="changeEveryoneAccess">
                        Zmie
                    </SmallButton>
                </div>
            </section>
            <section class="access-section regular" v-else>
                <h1>Zarzdzaj dostpem</h1>
                <header>
                    <h3>Nazwa uytkownika</h3>
                    <h3 class="expiry-head">Wano konta</h3>
                    <h3 class="activity-head">tekst3_tab3</h3>
                    <div class="check-all-wrapper">
                        <Checkbox :checked="areAllChecked"
                            @click="userCheckAllInput()" />
                    </div>
                </header>
                <AccessRowRegular
                    v-for="(account, index) in accounts"
                    :key="account.key"
                    :account="account"
                    :editable="accountEditModes[account.key]"
                    :opened="openedAccessRow === account.key"
                    :checked="!!userCheckStates[account.key]"
                    :tabs="tabs"
                    :highlightError="batchAddCardError"
                    @click="handleAccessRowClicked(account.key)"
                    @edit="toggleEditAccess(account.key)"
                    @save="saveExpiryDate(index, $event)"
                    @check="userCheckInput(account.key)"
                    @deletecard="handleDeleteCard(account.key)"
                    @addcard="handleAddCard(account.key, $event)"
                    @choosepayment="handleChoosePayment(account.key, $event)"
                    />
                <BatchAddCardPanel class="add-card-panel"
                    :selectedCount="selectedAccounts.length"
                    @submit="openBatchAddCardPopup"
                    @other="otherProductPopup = true"
                    />
                <div class="error-panel">
                    <p class="error-message" v-if="batchAddCardError">
                        aden uytkownik nie zosta wybrany
                    </p>
                </div>
                <BatchAddCardPopup :selectedCount="selectedAccounts.length"
                    :enabled="batchAddCardPopup"
                    @close="batchAddCardPopup = false"
                    @submit="addCardToSelected" />
                <OtherProductPopup
                    :enabled="otherProductPopup"
                    @close="otherProductPopup = false" />
            </section>
            <section class="documents-section">
                <h1>Pobierz logi (pdf)</h1>
                <header>
                    <h3>Numer dokumentu</h3>
                    <h3>Data wygenerowania</h3>
                </header>
                <div class="document-list">
                    <div class="document-row" v-for="document in documents" :key="document.key">
                        <h5>{{ document.id }}</h5>
                        <h5>{{ document.date }}</h5>
                        <div class="actions">
                            <SmallButton border-color="primary">
                                Pobierz
                            </SmallButton>
                        </div>
                    </div>
                </div>
            </section>
        </PageContent>
    </div>
</template>

<script>
    import SmallButton from '@/components/input/SmallButton.vue';
    import Dropdown from '@/components/input/Dropdown.vue';
    import Checkbox from '../components/input/Checkbox.vue';
    import MainPageHeader from '../components/MainPageHeader.vue';
    import PageContent from '../components/generic/PageContent.vue';
    import AccessRow from '../components/tab3/AccessRow.vue';
    import AccessRowRegular from '../components/tab3/AccessRowRegular.vue';
    import BatchAddCardPanel from '../components/tab3/BatchAddCardPanel.vue';
    import BatchAddCardPopup from '../components/tab3/BatchAddCardPopup.vue';
    import OtherProductPopup from '../components/tab3/OtherProductPopup.vue';
    import { getGroupService } from '../services/groupService';
    import { USER, ADMIN, SUBUSER } from '../types/role.types';
    import { limitView } from '../helpers/permissionHelper';
    import { getAuthService } from '../services/authService';
    import { getAccountService } from '../services/accountService';
    import { PAYMENT_CARD, PAYMENT_CASH } from '../types/account.types';

    function sortAccounts(accounts) {
        const sortedAccounts = accounts.slice();

        const isActive = account => (account.payment && account.payment.type === PAYMENT_CASH)
                    || (account.payment && account.payment.type === PAYMENT_CARD
                    && account.payment.cardNumber !== undefined);

        sortedAccounts.sort((a, b) => {
            const aIsActive = isActive(a);
            const bIsActive = isActive(b);

            if (aIsActive && !bIsActive) {
                return -1;
            }
            if (!aIsActive && bIsActive) {
                return 1;
            }
            return 0;
        });

        return sortedAccounts;
    }

    export default {
        name: 'Tab3',
        components: { 
            SmallButton,
            Dropdown,
            Checkbox,
            MainPageHeader,
            PageContent,
            AccessRow,
            AccessRowRegular,
            BatchAddCardPanel,
            BatchAddCardPopup,
            OtherProductPopup,
        },
        data() {
            return {
                role: null,
                accounts: [],
                accountEditModes: {},
                openedAccessRow: null,
                tabs: [
                    {
                        paymentTypes: [PAYMENT_CARD],
                        name: 'Tab1',
                        description: `Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                            sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
                            aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate
                            velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat
                            non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.`,
                    },
                    {
                        paymentTypes: [PAYMENT_CARD, PAYMENT_CASH],
                        name: 'Tab2',
                        description: `Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                            sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
                            aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate
                            velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat
                            non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.`,
                    },
                    {
                        paymentTypes: [PAYMENT_CARD, PAYMENT_CASH],
                        name: 'Tab3',
                        description: `Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                            sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
                            aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate
                            velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat
                            non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.`,
                    },
                ],
                items: [
                    {
                        label: '12',
                    },
                    {
                        label: '6',
                    },
                    {
                        label: '1',
                    },
                ],
                documents: [
                    {
                        id: '1/1/20',
                        date: '31.01.2020',
                    },
                    {
                        id: '2/1/20',
                        date: '31.02.2020',
                    },
                    {
                        id: '3/1/20',
                        date: '31.03.2020',
                    },
                ],
                userCheckStates: {},
                cardNumber: '',
                batchAddCardPopup: false,
                otherProductPopup: false,
                batchAddCardError: false,
            };
        },
        async mounted() {
            if (await limitView(this.$router, [ADMIN, USER, SUBUSER])) {
                return;
            }

            this.accounts = sortAccounts(await getGroupService().getFellowGroupMembers(true));
            this.role = await getAuthService().getRole();

            const newStates = {};
            this.accounts.forEach((a) => {
                newStates[a.key] = true;
            });
            this.userCheckStates = newStates;
        },
        computed: {
            isAdmin() {
                return this.role === ADMIN;
            },
            areAllChecked() {
                return !this.accounts.some(a => !this.userCheckStates[a.key]);
            },
            addCardPanelClass() {
                return {
                    enabled: this.accounts.some(a => this.userCheckStates[a.key]),
                };
            },
            selectedAccounts() {
                return this.accounts.filter(a => this.userCheckStates[a.key]
                    && a.payment !== undefined
                    && a.payment.type === PAYMENT_CARD
                    && a.payment.cardNumber === undefined);
            },
        },
        methods: {
            dropdownChoose(element) {
                this.$set(this, 'url', element.href);
            },
            changeEveryoneAccess() {

            },
            toggleEditAccess(accountKey) {
                this.accountEditModes = {
                    [accountKey]: !this.accountEditModes[accountKey],
                };
            },
            saveExpiryDate(index, accountPartial) {
                const newAccounts = this.accounts.slice();

                newAccounts[index] = {
                    ...newAccounts[index],
                    ...accountPartial,
                };
                
                // TODO Connected to back-end

                this.accounts = sortAccounts(newAccounts);
                this.accountEditModes = {};
            },

            handleAccessRowClicked(key) {
                if (this.openedAccessRow === key) {
                    this.openedAccessRow = null;
                } else {
                    this.openedAccessRow = key;
                }
            },
            
            userCheckAllInput() {
                if (this.accounts.some(a => !this.userCheckStates[a.key])) {
                    const newStates = {};
                    this.accounts.forEach((a) => {
                        newStates[a.key] = true;
                    });
                    this.userCheckStates = newStates;
                } else {
                    this.userCheckStates = {};
                }
                this.batchAddCardError = false;
            },

            userCheckInput(key) {
                const newStates = {};
                this.accounts.forEach((a) => {
                    newStates[a.key] = this.userCheckStates[a.key];
                });
                newStates[key] = !newStates[key];
                this.userCheckStates = newStates;
                this.batchAddCardError = false;
            },

            formatCardNumber() {
                // Removing all non-digit characters.
                const value = this.cardNumber.replace(/\D/g, '').substring(0, 16);

                let result = '';
                for (let i = 0; i < value.length; i += 1) {
                    result += value[i];
                    if (i % 4 === 3 && i !== value.length - 1) {
                        result += ' ';
                    }
                }

                this.cardNumber = result;
            },

            async handleDeleteCard(accountKey) {
                const result = await getAccountService().deleteCardForAccount(accountKey);
                if (result === true) {
                    const newAccounts = this.accounts.slice();
                    const account = this.accounts.find(a => a.key === accountKey);

                    if (account !== undefined) {
                        account.payment = {
                            ...account.payment,
                            cardNumber: undefined,
                        };
                    }

                    this.accounts = sortAccounts(newAccounts);
                    this.openedAccessRow = null;
                }
            },

            async handleAddCard(accountKey, options) {
                const result = await getAccountService().addCardForAccount(accountKey, options);
                if (result === true) {
                    const newAccounts = this.accounts.slice();
                    const account = this.accounts.find(a => a.key === accountKey);

                    if (account !== undefined
                        && account.payment !== undefined
                        && account.payment.type === PAYMENT_CARD) {
                        account.payment = {
                            ...account.payment,
                            cardNumber: options.cardNumber,
                        };
                    }

                    this.accounts = sortAccounts(newAccounts);
                    this.openedAccessRow = null;
                }
            },

            openBatchAddCardPopup() {
                if (this.selectedAccounts.length === 0) {
                    this.batchAddCardError = true;
                    return;
                }

                this.batchAddCardPopup = true;
                this.batchAddCardError = false;
            },

            addCardToSelected(options) {
                if (this.selectedAccounts.length === 0) {
                    this.batchAddCardError = true;
                    return;
                }

                this.batchAddCardPopup = false;

                this.accounts.forEach((account) => {
                    if (this.userCheckStates[account.key]) {
                        this.handleAddCard(account.key, options);
                    }
                });
            },

            async handleChoosePayment(accountKey, paymentMethod) {
                const result = await getAccountService().setPaymentMethod(accountKey, paymentMethod);

                if (result === true) {
                    const newAccounts = this.accounts.slice();
                    const accountIndex = Object.keys(this.accounts).find(
                        index => this.accounts[index].key === accountKey,
                    );

                    newAccounts[accountIndex] = {
                        ...newAccounts[accountIndex],
                        payment: {
                            ...newAccounts[accountIndex].payment,
                            type: paymentMethod,
                        },
                    };

                    this.accounts = sortAccounts(newAccounts);
                }
            },
        },
    };
</script>

